import { BLOG_DETAIL, BLOG_LIST, ADD_BLOG_COMMENT } from "@/api/api";
import featuredJobs from "@/components/featuredJobs";
import latestIndustryNews from "@/components/latestIndustryNews";
export default {
  name: "detail",

  data() {
    return {
      showSocialShareExpandStatus: false,
      locationUrl: '',
      showLoadingStatus: false,
      detailData: {},
      basicForm: {
        blog_id: undefined,
        content: '',
        name: '',
        email: '',
        image_url: '',
        token: localStorage.getItem('token')
      },
      basicRules: {
        content: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }],
        email: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }]
      }
    };
  },

  components: {
    featuredJobs,
    latestIndustryNews
  },

  setup() {},

  created() {
    let id = this.$route.query.id;
    this.getBlogDetail(id);
    this.locationUrl = window.location.href;
  },

  mounted() {},

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.basicForm.blog_id = this.$route.query.id;
          let params = Object.assign({}, this.basicForm);
          ADD_BLOG_COMMENT(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              console.log('a');
            }
          }).catch(err => {
            console.log(err);
            this.$message.error(err.msg);
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    getBlogDetail(id) {
      let params = {
        id: id
      };
      BLOG_DETAIL(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.detailData = res.message;
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    getBlogList() {
      let params = {
        page: 1,
        limit: 5
      };
      BLOG_LIST(params).then(res => {
        console.log(res);
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    }

  }
};