// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue'; // Import Swiper styles

import 'swiper/css';
import "swiper/css/zoom";
import "swiper/css/pagination";
import "swiper/css/navigation";
import SwiperCore, { Pagination, Autoplay, Navigation, Zoom } from 'swiper';
import { JOB_FEATURED_LIST, APPLY_JOBS, ADD_FAVORITE, CANCEL_FAVORITE } from "@/api/api";
SwiperCore.use([Pagination, Autoplay, Navigation, Zoom]);
export default {
  name: "featuredJobs",

  data() {
    return {
      jobFeaturedListData: []
    };
  },

  components: {
    Swiper,
    SwiperSlide
  },

  mounted() {
    this.getJobFeaturedList();
  },

  methods: {
    getJobFeaturedList() {
      let params = {
        ad_type: 2
      };
      JOB_FEATURED_LIST(params).then(res => {
        console.log(res);

        if (res.code === 200) {
          this.jobFeaturedListData = res.message;
        } else {
          console.log(res.msg);
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    applyJobs(id) {
      let identity = localStorage.getItem('identity');
      let token = localStorage.getItem('token');

      if (identity == 1) {
        let params = {
          job_id: id,
          token: token
        };
        APPLY_JOBS(params).then(res => {
          if (res.code == 200) {
            this.$message.success('Apply Success');
          }
        }).catch(err => {
          console.log(err);

          if (err.code === 400) {
            this.$message.error('Please complete your profile in order to apply');
          } else {
            this.$message.error(err.msg);
          }
        });
      } else {
        this.$message.warning('Please switch to an educator profile to be able to apply');
      }
    },

    addFavorite(id, type, title, url, index) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: id,
        type_title: title,
        type_url: url
      };
      ADD_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.$message.success('Success');
          this.jobFeaturedListData[index]['is_favorite'] = 1;
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    cancelFavorite(type, typeId, index) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: typeId
      };
      CANCEL_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.jobFeaturedListData[index]['is_favorite'] = 0;
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    }

  }
};